<template>
  <div class="remote-bottom-style">
    <div class="remote-style">
      <div class="remote-style-top">
        <div
          class="charts-main border-style"
          v-loading="!businessMonthRes"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="加载中"
        >
          <div class="title">
            <span>月业务量</span>
          </div>
          <div>
            <countLabel
              v-if="businessMonthRes"
              :countData="businessMonthLabel"
              :countUnit="phonesUnit"
            ></countLabel>
          </div>
          <div class="charts-style">
            <bda-bar-charts
              v-if="businessMonthRes"
              :chartsConfig="businessMonthData"
              :barType="barType"
              :bar-width="4"
              :fontSizeShow="12"
              :changeAxios="false"
              :legendStatus="false"
            ></bda-bar-charts>
          </div>
        </div>
        <div
          class="tj-main"
          v-loading="!businessDayRes"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="加载中"
        >
          <div class="title">
            <span>年业务量</span>
          </div>
          <div class="tj-all-count">{{ yearBusinessCount.yearCount }}</div>
          <div class="kb-count">
            <span class="title-tr">最高月业务量</span>
            <div class="value-tr">
              <span>{{
                dateTranslate("mounth", yearBusinessCount.mounthCount.date)
              }}</span>
              <span>{{ parseInt(yearBusinessCount.mounthCount.count) }}</span>
            </div>

            <div class="value-tr2" style="color: #86ff80">
              <span>今日抢修类</span>
              <span>{{ parseInt(yearBusinessCount["抢修类"]) }}</span>
            </div>
            <div class="value-tr2" style="color: #86ff80">
              <span>今日非抢修类</span>
              <span>{{ parseInt(yearBusinessCount["非抢修类"]) }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="remote-style-bottom">
        <div
          class="charts-main1 border-style"
          v-loading="!businessDayRes"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="加载中"
        >
          <div class="title">日业务量</div>
          <div>
            <countLabel
              v-if="businessDayRes"
              :countData="businessDayLabel"
              :countUnit="phonesUnit"
            ></countLabel>
          </div>
          <div class="charts-style">
            <bda-bar-charts
              v-if="businessDayRes"
              :chartsConfig="businessDayData"
              :barType="barType"
              :bar-width="4"
              :fontSizeShow="12"
              :changeAxios="false"
              :legendStatus="false"
            ></bda-bar-charts>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/remote-work-monitoring';
import qs from 'qs';
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';

import countLabel from "@/components/countLabel/countLabel";

export default {
  name: "remoteRight",
  mixins: [Interface],
  components: {
    countLabel,
    bdaBarCharts
  },
  data() {
    return {
      yearBusinessCount: {
        yearCount: 0,
        mounthCount: {
          date: '',
          count: 0
        },
        '抢修类': 0,
        '非抢修类': 0
      },
      phonesUnit: '',
      businessMonthLabel: [],
      businessDayLabel: [],
      businessMonthData: {},
      businessDayData: {},
      businessMonthRes: false,
      businessDayRes: false,
      barType: 'bar',
      businessCountRes: true
    }
  },
  watch: {},
  computed: {},
  created() {

  },
  mounted() {
    if (this.$parent.dateParams) {

      this.getBusinessVolumeForYear(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.businessCountRes = false;
        this.bussinessForYear(res);
      }).catch(() => {
        this.businessCountRes = false;
      });
      // codeType
      this.getObjByCodeType(qs.stringify({
        codeType: 'csBusiType'
      })).then(data => {
        this.getBusinessVolumeForMonth(qs.stringify({
          timeStr: this.$parent.dateParams
        })).then(res => {
          this.bussinessCallback(res, data, 'month');
        }).catch(() => {
          this.businessMonthRes = false;
        });
        this.getBusinessVolumeForDay(qs.stringify({
          timeStr: this.$parent.dateParams
        })).then(res => {
          this.bussinessCallback(res, data, 'day');
        }).catch(() => {
          this.businessDayRes = false;
        });
      })
    }


  },
  methods: {
    dateTranslate(type, dateStr) {
      let date = "";
      if (dateStr) {
        let str = ''
        if (dateStr.length === 6) {
          str = dateStr.substring(0, 4) + '/' + dateStr.substring(4, 6) + '/01'
        } else if (dateStr.length === 8) {
          str = dateStr.substring(0, 4) + '/' + dateStr.substring(4, 6) + '/' + dateStr.substring(6, 8)
        }
        if (str) {
          let dqDate = new Date(str);
          if (type === 'mounth') {
            date = (dqDate.getMonth() + 1) + '月'
          } else {
            date = (dqDate.getMonth() + 1) + '月' + dqDate.getDate() + '日'
          }
        }

      }
      return date;
    },
    bussinessForYear(data) {
      if (data) {
        this.yearBusinessCount.yearCount = data.businessVolumeForYear !== undefined ? data.businessVolumeForYear : 0;
        if (data.maxBusinessVolumeForMonth) {
          this.yearBusinessCount.mounthCount = {
            count: data.maxBusinessVolumeForMonth.ccsum !== undefined ? data.maxBusinessVolumeForMonth.ccsum : 0,
            date: data.maxBusinessVolumeForMonth.callday ? data.maxBusinessVolumeForMonth.callday : ''
          }
        }
        if (data.businessVolumeForDay) {
          for (let item of data.businessVolumeForDay) {
            if (item.isrepair === '1') {
              this.yearBusinessCount['抢修类'] = item.ccsum !== undefined ? item.ccsum : 0
            } else {
              this.yearBusinessCount['非抢修类'] = item.ccsum !== undefined ? item.ccsum : 0
            }
          }

        }
      }
    },

    businessDataFilter(data, translateMap, name) {
      let dimension = [], quotaData = [];
      if (data) {
        for (let item of data) {
          if (item.busitypecode) {
            dimension.push(translateMap[item.busitypecode]);
            quotaData.push(item.ccsum)
          }

        }
      }
      let _data = {
        dimension: dimension,
        quotaMap: {}
      };
      _data.quotaMap[name] = quotaData;
      return _data
    },
    bussinessCallback(data, translateMap, type) {
      if (data) {
        if (type === 'month') {
          this.businessMonthLabel = [{
            showText: '本月业务量',
            value: data.businessVolumeForMonthValue !== undefined ? data.businessVolumeForMonthValue : 0,
            key: Math.random() + '',
            color: '#019eff',
          }];
          if (data.businessVolumeForMonthLine) {
            this.businessMonthData = this.businessDataFilter(data.businessVolumeForMonthLine, translateMap, '本月业务量');
            this.businessMonthData.colorMap = { '本月业务量': ['#00FFEF', '#007EFF'] }
          }
          this.businessMonthRes = false;
          this.$nextTick(() => {
            this.businessMonthRes = true;
          })

        } else {
          this.businessDayLabel = [{
            showText: '今日业务量',
            value: data.businessVolumeForDayValue !== undefined ? data.businessVolumeForDayValue : 0,
            key: Math.random() + '',
            color: '#00FFD4',
          }];
          if (data.businessVolumeForDayLine) {
            this.businessDayData = this.businessDataFilter(data.businessVolumeForDayLine, translateMap, '今日业务量');
            this.businessDayData.colorMap = { '今日业务量': ['#6AFFA2', '#00BDDE'] }
          }
          this.businessDayRes = false;
          this.$nextTick(() => {
            this.businessDayRes = true;
          })
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
.remote-bottom-style {
  width: 100%;
  height: calc(100% - 10px);
  position: relative;
  // padding: 0 20px;
  font-size: 16px;
  box-sizing: border-box;
  // .border-style {
  //   border: 1px solid #3e7ca6;
  //   border-radius: 5px;
  //   box-shadow: #0a65ce 0px 0px 5px inset;
  // }

  .remote-style {
    width: 100%;

    float: left;
    position: relative;
    height: 100%;
    // display: flex;
    .remote-style-top {
      display: flex;
      height: 50%;
    }
    .remote-style-bottom {
      display: flex;
      height: calc(50% - 20px);
      margin-top: 10px;
    }
    .tj-main {
      flex: 1;
      height: 100%;
      float: left;
      // border: 1px solid #0a65ce;
      // border-radius: 5px;
      background: url("../../../assets/img/远程框1.png") no-repeat top center;
      background-size: 100% 100%;
      padding: 10px 10px 10px 10px;
      box-sizing: border-box;
      position: relative;

      .title {
        height: 19%;
        color: #00e7ff;
        // letter-spacing: 3px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .tj-all-count {
        // margin-bottom: 15px;
        // line-height: 55px;
        height: 17%;
        font-size: 20px;
        font-weight: 600;
        color: #00e7ff;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .kb-count {
        width: 100%;
        // height: calc(100% - 90px);
        position: relative;
        padding: 25px 25px;
        box-sizing: border-box;
        height: 64%;

        color: #fff;
        .title-tr {
          height: 25%;
          display: flex;
          align-items: center;

          // height: 20px;
          // line-height: 20px;
        }
        .value-tr,
        .value-tr2 {
          height: 25%;
          color: #00ffd4;
          display: flex;
          align-items: flex-start;
          span {
            display: inline-block;
            width: 60%;
            // &:first-child {
            //   color: #fff;
            // }

            &:nth-child(1) {
              display: flex;
              align-items: center;
            }

            &:nth-child(2) {
              width: 40%;
              text-align: right;
              font-size: 18px;
              display: flex;
              justify-content: flex-end;
              align-items: center;
            }
          }
        }
        .value-tr2 {
          height: 25%;
          align-items: center;

          span {
            &:nth-child(1) {
              width: 60%;
              color: #fff;
            }
            &:nth-child(2) {
              width: 40%;
            }
          }
        }
      }
    }

    .charts-main,
    .charts-main1 {
      flex: 2.5;
      height: 100%;
      padding: 10px;
      box-sizing: border-box;
      float: left;
      position: relative;
      background: url("../../../assets/img/远程框2.png") no-repeat top center;
      background-size: 100% 100%;
      .title {
        // background: linear-gradient(
        //   to bottom,
        //   rgba(15, 146, 229, 0.3),
        //   #061b3a
        // );
        color: #00e7ff;
        // line-height: 38px;
        height: 18%;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .charts-style {
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        // height: calc(100% - 90px);
        height: calc(82% - 40px);

        // height: 100%;
        position: relative;
      }
      .charts-style3 {
        width: 100%;
        // height: calc(100% - 70px);
        height: calc(82% - 40px);

        position: relative;
      }
    }
    .charts-main1 {
      //   flex: 2;
      //   height: 50%;
      background: url("../../../assets/img/远程框3.png") no-repeat top center;
      background-size: 100% 100%;
      margin: 0;
    }
  }

  .remote-style:nth-child(2) {
    margin-left: 15px;
  }
}
</style>